.container {
  width: 80%;
  height: auto;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  z-index: 100; }
  .container .quoteWrapper {
    width: 100%;
    min-height: auto;
